}
nav li a {
  color:rgb(161, 98, 53);;
  text-decoration: none;
  
}



.display-container img {
  height: auto;
  width: 150px;
  margin-top:20px;
}

.majorcontainer {
  margin-top: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}







