}
.recipelist_cookie:hover .img__description {
  visibility: visible;
  opacity: 1;
}

.recipelist_cookie:hover img {
    -webkit-filter:blur(2px);
            filter:blur(2px);
}


.img__description {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  color: #fff;
  visibility: hidden;
  opacity: 0;
  font-family: 'Amatic SC', cursive;
  font-size: 1.05rem;
  text-align: center;
  padding-top: 35px;
}

.button {
    transition-duration: 0.4s;
    border: .5px solid; 
