    border-radius: 4px;
  }
  
  
  .button:hover {
    background-color: #f48fb1; 
    color: white;
    cursor: pointer;
  }


.displayAnimation {
    -webkit-animation: fadeIn 1s ease-out;
            animation: fadeIn 1s ease-out;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards;
}

@-webkit-keyframes fadeIn {
    from {
      opacity: 0;
    }
  }

@keyframes fadeIn {
    from {
      opacity: 0;
    }
  }

