.previous-btn{
    display: inline-block;
    text-decoration: none;
    background: rgba(250, 185, 87, 0.986);
    color: rgba(71, 41, 1, 0.47);
    width: 70px;
    height: 70px;
    line-height: 100px;
    border-radius: 50%;
    overflow: hidden;
    box-shadow: 0px 0px 0px 10px #87b2e4;
    transition: .4s;
    font-family: 'Montserrat', sans-serif;
    font-size: 20px;
    font-weight: bolder;
    cursor: pointer;
    position: absolute;
    left: 100px;    
    padding: 0;
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

@media (max-width: 735px)  {
    .previous-btn {
        width: 40px;
        height: 40px;
        line-height: 50px;
        border-radius: 50%;
        left: 70px; 
        font-size: 15px;

    }
  }

  


.previous-btn:hover{
    background: rgba(236, 196, 252, 0.986);
    box-shadow: 0px 0px 0px 20px #87b9f1;

}

